  background: #111521; }

body {
  line-height: 1; }

article,
aside,
footer,
header,
nav,
section,
main {
  display: block; }

* {
  box-sizing: border-box; }

*:before,
*:after {
  box-sizing: inherit; }

article,
aside,
nav,
footer,
header,
section {
  display: block; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

ol,
ul {
  list-style: none; }

img,
video {
  max-width: 100%;
  vertical-align: bottom; }

img {
  border-style: none; }

q,
blockquote {
  quotes: none; }

blockquote:after,
blockquote:before,
q:after,
q:before {
  content: "";
  content: none; }

button {
  appearance: none;
  border: inherit;
  outline: inherit;
  cursor: pointer; }

[hidden],
.hidden {
  display: none !important; }

[disabled],
.disabled {
  cursor: not-allowed; }

:focus {
  outline: 0;
  box-shadow: 0 0 0 2px #5bbce4, inset 0 0 0 2px #5bbce4; }

:focus:not(:focus-visible) {
  outline: 0; }

p {
